.orange_ribbon{
    width: 100%;
    background-color: #bd6a26;
    height: 15px;
    margin-top: 60px;
}

.contacts{
    background-color: #323231;
    width:100%;
    height: 200px;
    height: max-content;
}
.contacts_text{
    text-align: center;
    color: white;
    padding-top: 35px;
    margin-bottom: 25px;
    font-size: 30px;
}


.number{
    text-align: center;
    margin: 10px;
    color: #bdbdbf;
    font-size: 25px;
    font-family: "Play regular";
}

.small_orange_ribbon{
    width: 90%;
    background-color: #bd6a26;
    height: 2px;
    margin: auto;
    margin-top: 80px;
}

.copyright{
    display: flex;
    padding: 30px 2% 25px 2%;
    justify-content: space-between;
}
.copyright_item{
    color: #bdbdbf;
    font-size: 20px;
}

@media (max-width: 700px){
    .contacts_text{
     font-size: 22px;
    }
    .number {
        font-size: 18px;
    }
    .copyright_item{
        font-size: 15px;
    }
 }